import { __decorate } from "tslib";
import { Vue, Component } from 'vue-property-decorator';
import { Form, Button, Input, Icon, Checkbox } from 'ant-design-vue';
let Login = class Login extends Vue {
    constructor() {
        super(...arguments);
        this.loginForm = { username: '', password: '' };
        this.loading = false;
    }
    beforeCreate() {
        this.form = this.$form.createForm(this);
    }
    handleSubmit(e) {
        e.preventDefault();
        this.form.validateFields((err, values) => {
            if (!err) {
                this.loading = true;
                this.$store.dispatch('LoginByUsername', values)
                    .then(() => {
                    this.loading = false;
                    this.$message.success('登陆成功');
                    this.$router.push('/dashboard');
                }).catch(() => {
                    this.loading = false;
                });
            }
        });
    }
};
Login = __decorate([
    Component({
        components: {
            'a-form': Form,
            'a-form-item': Form.Item,
            'a-button': Button,
            'a-input': Input,
            'a-icon': Icon,
            'a-checkbox': Checkbox
        }
    })
], Login);
export default Login;
